import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit, TemplateRef } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-control-export-goods',
  templateUrl: './control-export-goods.component.html',
  styleUrls: ['./control-export-goods.component.scss']
})
export class ControlExportGoodsComponent implements OnInit {
  searchValue: any;
  salesOrder: Array<any> = [];
  salesSubOrder: Array<any> = [];
  heightScroll: any;
  public date: Date[] = [new Date(), new Date()];
  load_status: boolean = false;
  dataRestore: any;
  bill_selected: any = {};
  /**Giới hạn thời gian cho xem bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < this.minDate.getTime() - 24 * 60 * 60 * 1000) return true;
    return false;
  };
  infoShop: any = {};
  minDate: Date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 30);
  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
    private modal: NzModalService,
    private vhAuth: VhAuth,
    private datePipe: DatePipe
  ) {
    // this.date[0].setHours(0, 0, 0, 0)
    // this.date[1].setHours(23, 59, 59, 59)
    this.dataRestore = this.router.getCurrentNavigation().extras.state.dataRestore;
  }

  ngOnInit(): void {
    if (this.dataRestore.id_shop_selected) {
      this.searchValue = this.dataRestore.searchValue;
      this.salesOrder = this.dataRestore.salesOrder;
      this.salesSubOrder = this.dataRestore.salesSubOrder;
      this.load_status = true;
      this.date = this.dataRestore.date;
    }
    this.infoShop = this.vhQuerySales.getlocalShopListSendo()[0];
    if ((this.minDate.getTime() - 24 * 60 * 60 * 1000) < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
      this.disabledDate = (current: Date): boolean => {
        if (current.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000) return true;
        return false;
      };
    }
  }
  /**Tìm kiếm hóa đơn
   *  @example this.search('')
   */
  search(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase());
      this.salesOrder = this.vhAlgorithm.searchList(val, this.salesSubOrder, ['order_number']);
    } else this.salesOrder = [...this.salesSubOrder];
  }
  /**
   * về trang quản lý đối soát
   * @example this.goBack()
   */
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/sendo/control'], {
      state: { dataRestore: this.dataRestore }
    });
  }
  /**
   * bắt sự kiện thay đổi ngày xem báo cáo
   * @example this.changeTime('2023-06-09T04:14:30.367Z')
   */
  changeTime(event) {
    if (event[1].getTime() - event[0].getTime() <= 2678400000) {
    } else {
      this.vhComponent.showToast(2000, this.lang.translate("The time to view no more than 30 days"), "alert-toast");
      this.date = [new Date(event[0]), new Date(event[0].getTime() + 2678313600)];
    }
  }
  /**
   * bắt sk nhấn nút 'Xem' ở giao diện
   * @example this.showReport()
   */
  showReport() {
    this.load_status = true;
    this.salesOrder = [];
    this.salesSubOrder = [];
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.getSalesOrderDetailListSendo(new Date(this.date[0].setHours(0, 0, 0)).toISOString(), new Date(this.date[1].setHours(23, 59, 59, 59)).toISOString(), this.infoShop.token, this.infoShop._id).then((result: any) => {
        this.vhComponent.hideLoading(0);
        result.forEach(item => {
          this.salesOrder.push({
            ...item.sales_order,
            sku_details: item.sku_details,
            created_date_time_stamp: new Date((item.sales_order.created_date_time_stamp - 7 * 60 * 60) * 1000),
            id_shop: this.infoShop._id,
            name_control_goods: item.sales_order.verify_status_delivered_goods ? `${this.vhAuth.getlocalEmployee(item.sales_order.id_verifier_delivered_goods).name} ${this.lang.translate("has checked")} ${this.format_date(item.sales_order.verify_date_delivered_goods)}` : null
          });
        });
        this.salesSubOrder = [...this.salesOrder];


      }).catch(err => this.vhComponent.hideLoading(0));
    });
  }

  ngAfterViewChecked() {
    if (document.querySelector("#control-export-goods") && document.querySelector(".ant-tabs-tab") && document.querySelector(".ant-table-thead") && document.querySelector(".control-export-goods-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#control-export-goods").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".control-export-goods-header").clientHeight - document.querySelector(".ant-tabs-tab").clientHeight - 90 + "px";
    }

    this.cdRef.detectChanges();
  }
  /**
   * hàm này để cập nhật đơn hàng lưu vào database của mình
   * @param bill
   * @param data
   */
  updateControlGoods(bill, updateForControl: TemplateRef<{}>) {
    if (bill['verify_status_delivered_goods']) {
      this.bill_selected = bill;
      this.bill_selected['verify_date_delivered_goods'] = new Date().toISOString();
      this.modal.confirm({
        nzTitle: this.lang.translate("Check this order"),
        nzContent: updateForControl,
        nzOkText: 'OK',
        nzOnOk: () => {
          return new Promise((resolve, reject) => {
            let value: any = {};
            value['verify_status_delivered_goods'] = true;
            value['verify_date_delivered_goods'] = this.bill_selected['verify_date_delivered_goods'];
            value['id_verifier_delivered_goods'] = this.vhAuth.getUser()._id;
            this.vhQuerySales.updateSalesOrderSendo_byFile([bill.order_number.toString()], value, this.infoShop.token, this.infoShop._id).then((bool) => {

              if (bool) {
                bill[`name_control_goods`] = `${this.vhAuth.getlocalEmployee(value['id_verifier_delivered_goods']).name} đã đối soát ${this.format_date(value['verify_date_delivered_goods'])}`;
                resolve(true);
              } else reject(false);
            }).catch(err => { console.error(err); reject(false); });
          });
        },
        nzOnCancel: () => { bill['verify_status_delivered_goods'] = false; },
        nzCancelText: this.lang.translate("Cancel")
      });
    } else {
      this.modal.confirm({
        nzTitle: this.lang.translate("Confirm") + "?",
        nzContent: this.lang.translate("Cancel check this order"),
        nzOkText: 'OK',
        nzOnOk: () => {
          return new Promise((resolve, reject) => {
            let value: any = {};
            value['verify_status_delivered_goods'] = false;
            this.vhQuerySales.updateSalesOrderSendo_byFile([bill.order_number], value, this.infoShop.token, this.infoShop._id).then(res => {
              if (res) resolve(true);
              else reject(false);
            });
          });
        },
        nzOnCancel: () => { bill['verify_status_delivered_goods'] = true; },
        nzCancelText: this.lang.translate("Cancel")
      });
    }
  }
  /**Render date theo định dạng ngày/tháng/năm */
  format_date(date) {
    return this.datePipe.transform(date, "dd/MM/yyyy");
  }

  /**
   * qua trang xem chi tiết hóa đơn
   * @example this.gotoDetail(bill)
   */
  gotoDetail(bill) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/sendo/control/export-goods/detail'], { state: { bill: bill, dataRestore: { salesOrder: this.salesOrder, searchValue: this.searchValue, id_shop_selected: this.infoShop._id, listProduct: this.dataRestore.listProduct, date: this.date, salesSubOrder: this.salesSubOrder } } });
  }
}
